@use "../util" as *;
@use "variables" as *;

.container {
	max-width: rem(1250);
	padding-inline: rem(12);
	margin-inline: auto;
	max-width: 69.375rem;
	margin: 0 auto;

	@include breakpoint-up(medium) {
		padding-inline: rem(24);
		transform: translate(0, 0);
	}

	@include breakpoint($bp: rem(1320)) {
		padding: 0;
		transform: translate(0, 0);
	}
}
