:root {
  --font-body: "Public Sans", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

@media (max-width: 50.125em) {
  .hide_for_mobile {
    display: none !important;
  }
}

@media (min-width: 50rem) {
  .hide_for_desktop {
    display: none !important;
  }
}

.navbar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}

.navbar {
  padding: 0.75rem;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-ai-c {
  align-items: center;
}
.flex-jc-c {
  justify-content: center;
}
.flex-col {
  flex-direction: column;
}

.grid {
  display: grid;
}

.block {
  display: block;
}

.btn {
  cursor: pointer;
  display: inline-block;
  border: 0;
  font-weight: 400;
}

.card {
  display: block;
  padding: 0.75rem;
  border: 1px solid #ddd;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
  border-radius: 50px;
}
.card__title {
  font-size: 1rem;
  padding-bottom: 0.75rem;
  font-weight: bold;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  /* added line */
  border: 0;
}

.uppercase {
  text-transform: uppercase;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.fade-out {
  display: block;
  animation: fade-out 1s ease-in-out;
}

.fade-in {
  display: block;
  animation: fade-in 1s ease-in-out;
  animation-fill-mode: forwards;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: inherit;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.4;
  min-height: 100vh;
  overflow-x: hidden;
}

.bodyOverflow {
  overflow: hidden;
}

ul,
ol {
  list-style: none;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li,
button {
  cursor: pointer;
}

a,
a:visited,
a:active {
  text-decoration: none;
  cursor: pointer;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5 {
  line-height: 1.1;
}

/* make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* make form elements easier to work with */
input,
button,
textarea,
select {
  font: inherit;
}

.container {
  max-width: 78.125rem;
  padding-inline: 0.75rem;
  margin-inline: auto;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .container {
    padding-inline: 1.5rem;
    transform: translate(0, 0);
  }
}
@media (min-width: 82.5rem) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.header {
  z-index: 10000000000;
  position: relative;
  background-color: #fff;
}
.header .primary_nav {
  height: 5rem;
}
.header__logo img {
  width: 8.8125rem;
  height: 1.375rem;
}
.header__hamburger {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}
.header__hamburger span {
  position: relative;
  width: 24px;
  height: 2px;
  background-color: #2d314d;
  transition: transform 0.3s ease-in-out;
}
.header__hamburger span::after, .header__hamburger span::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #2d314d;
  transition: transform 0.3s ease-in-out;
}
.header__hamburger span::before {
  transform: translateY(-7px);
}
.header__hamburger span::after {
  transform: translateY(7px);
}
.header__menu li {
  font-weight: 300;
  font-size: 0.875rem;
  transition: color 200ms ease-in-out;
  position: relative;
  color: #9698a6;
}
.header__menu li::after {
  content: "";
  position: absolute;
  background: linear-gradient(to right, #31d35c, #2bb7da);
  width: 100%;
  height: 4px;
  left: 0;
  bottom: -1.875rem;
  transform: scaleX(0);
  transition: transform 200ms ease-in-out;
  transform-origin: left;
}
.header__menu li:hover {
  color: #2d314d;
}
.header__menu li:hover::after {
  transform: scaleX(1);
}
.header__menu li:not(:first-child) {
  margin: 0 0 0 1.875rem;
}

.cta {
  padding: 0.685rem 2.1875rem;
  border-radius: 50px;
  background: linear-gradient(to right, #31d35c, #2bb7da);
  font-size: 0.875rem;
  font-family: "Public Sans", sans-serif;
  font-weight: 400;
  transition: all 200ms ease-in-out;
}
.cta a {
  color: white;
}
.cta:hover {
  opacity: 0.75;
}

.active span {
  background: transparent;
}
.active span::before {
  transform: rotate(45deg);
}
.active span::after {
  transform: rotate(-45deg);
}

.secondary_nav_bg {
  position: relative;
  width: 100%;
  z-index: 1;
}
.secondary_nav_bg svg {
  position: absolute;
  width: 100%;
  filter: brightness(30%);
  opacity: 0.5;
}

.header__side-menu {
  display: none;
  position: relative;
}
.header__side-menu__links {
  background: white;
  width: 90%;
  height: 45%;
  margin-top: 20px;
  padding: 35px 0;
  border-radius: 5px;
  position: relative;
  z-index: 1;
}
@media screen and (max-height: 37.5em) {
  .header__side-menu__links {
    padding: 15px 0;
    margin-top: 10px;
    transform: translate(0, 0);
  }
}
.header__side-menu__links li {
  font-size: 1.125rem;
  color: #9698a6;
  font-weight: 400;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 200ms ease-in-out;
}
.header__side-menu__links li:hover {
  color: #2d314d;
}
.header__side-menu__contents {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-between;
  flex-direction: column;
  width: 100%;
  padding: 35px 0;
}
@media screen and (max-height: 37.5em) {
  .header__side-menu__contents {
    padding: 25px 10px;
    transform: translate(0, 0);
  }
}
.header__side-menu__contents h2 {
  font-family: "Public Sans", sans-serif;
  font-weight: 400;
  font-size: 2.1875rem;
  color: #2d314d;
  margin-bottom: 1.875rem;
}
@media screen and (max-height: 37.5em) {
  .header__side-menu__contents h2 {
    font-size: 1.5625rem;
    margin-bottom: 0.625rem;
    transform: translate(0, 0);
  }
}
.header__side-menu__contents p {
  line-height: 1.6;
  font-family: "Public Sans", sans-serif;
  font-weight: 400;
  color: #9698a6;
  margin-bottom: 1.875rem;
}
@media screen and (max-height: 37.5em) {
  .header__side-menu__contents p {
    margin-bottom: 0.625rem;
    transform: translate(0, 0);
  }
}

.display {
  position: absolute;
  background: linear-gradient(to bottom, #2d314d, #fafafa);
  width: 100%;
  height: calc(100vh - 5rem);
  display: flex;
  flex-direction: column;
}

.first_section {
  background: #fafafa;
}
@media (min-width: 64rem) {
  .first_section {
    transform: translate(0, 0);
  }
  .first_section .main {
    max-width: 78.125rem;
    padding-inline: 0.75rem;
    margin-inline: auto;
    padding: 0 !important;
    display: flex;
    flex-direction: row-reverse;
    max-width: 69.375rem;
    margin: 0 auto;
    min-height: 41.875rem;
  }
  .first_section__contents {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  .first_section__contents h1,
.first_section__contents p {
    text-align: left;
  }
}
.first_section__img {
  position: relative;
  height: 16.25rem;
  width: 100%;
  background-image: url(/images/bg-intro-mobile.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
}
@media (min-width: 64rem) {
  .first_section__img {
    background-image: none;
    transform: translate(0, 0);
    min-height: 41.875rem;
  }
  .first_section__img::before {
    content: "";
    position: absolute;
    width: 153%;
    height: 100%;
    left: -8%;
    background-image: url(/images/Group.svg);
    background-repeat: no-repeat;
    background-size: 150%;
    background-position: 17% 70%;
  }
}
@media (min-width: 25rem) {
  .first_section__img {
    height: 22.5rem;
    transform: translate(0, 0);
  }
}
@media (min-width: 37.5rem) {
  .first_section__img {
    height: 28.75rem;
    transform: translate(0, 0);
  }
}
@media (min-width: 43.75rem) {
  .first_section__img {
    height: 35rem;
    transform: translate(0, 0);
  }
}
.first_section__img::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(/images/image-mockups.png);
  background-repeat: no-repeat;
  background-position: center 88%;
  background-size: 85%;
}
@media (min-width: 64rem) {
  .first_section__img::after {
    width: 130%;
    height: 135%;
    background-size: 115%;
    background-position: 20% 120%;
    left: 16%;
  }
}
.first_section__content__tittle {
  font-size: 2.625rem;
  color: #2d314d;
  font-family: "Public Sans", sans-serif;
  line-height: 1.1;
  font-weight: 400;
  margin-top: 2.8125rem;
  text-align: center;
}
@media (min-width: 56.25rem) {
  .first_section__content__tittle {
    font-size: 3.25rem;
    transform: translate(0, 0);
  }
}
@media (min-width: 43.75rem) {
  .first_section__content__tittle {
    font-size: 3.25rem;
    margin-top: 3.4375rem;
    transform: translate(0, 0);
  }
}
@media (max-width: 18.125rem) {
  .first_section__content__tittle {
    font-size: 2rem;
    transform: translate(0, 0);
  }
}
.first_section__content__info {
  color: #9698a6;
  font-family: "Public Sans", sans-serif;
  font-weight: 300;
  margin-bottom: 1.875rem;
  margin-top: 1.3125rem;
  text-align: center;
}
@media (min-width: 64rem) {
  .first_section__content__info {
    width: 78%;
    transform: translate(0, 0);
  }
}
.first_section__content__btn {
  display: grid;
  place-items: center;
}
.first_section__content__btn .cta {
  margin-bottom: 5rem;
}

.second_section {
  background: #f3f4f6;
  padding: 110px 0;
}
@media (max-width: 56.25rem) {
  .second_section__info h2,
.second_section__info p {
    text-align: center;
    place-items: center;
    transform: translate(0, 0);
  }
}
@media (max-width: 37.5rem) {
  .second_section__info h2 span {
    display: block;
  }
}
.second_section__info h2 {
  font-size: 2.25rem;
  color: #2d314d;
  font-family: "Public Sans", sans-serif;
  line-height: 1.1;
  font-weight: 400;
  margin-bottom: 38px;
}
@media (max-width: 18.125rem) {
  .second_section__info h2 {
    font-size: 1.875rem;
    transform: translate(0, 0);
  }
}
.second_section__info p {
  color: #9698a6;
  font-family: "Public Sans", sans-serif;
  margin-bottom: 75px;
  font-weight: 300;
}
.second_section__content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}
@media (max-width: 56.25rem) {
  .second_section__content {
    grid-template-columns: repeat(2, 1fr);
    transform: translate(0, 0);
    text-align: center;
    place-items: center;
  }
}
@media (max-width: 37.5rem) {
  .second_section__content {
    grid-template-columns: 1fr;
    transform: translate(0, 0);
  }
}
.second_section__content .details-svg {
  margin-bottom: 40px;
}
.second_section__content h3 {
  margin-bottom: 40px;
  color: #2d314d;
  font-family: "Public Sans", sans-serif;
  font-weight: 400;
  font-size: 1.5rem;
}
.second_section__content p {
  color: #9698a6;
  font-family: "Public Sans", sans-serif;
  font-size: 0.8125rem;
  font-weight: 300;
}

.third_section {
  padding: 95px 0 75px 0;
  background: #fafafa;
}
.third_section h3 {
  font-size: 2.25rem;
  color: #2d314d;
  font-family: "Public Sans", sans-serif;
  line-height: 1.1;
  font-weight: 400;
  margin-bottom: 52px;
}
@media (max-width: 56.25rem) {
  .third_section h3 {
    text-align: center;
    transform: translate(0, 0);
  }
}
@media (max-width: 18.125rem) {
  .third_section h3 {
    font-size: 1.875rem;
    transform: translate(0, 0);
  }
}
.third_section .article {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}
@media (max-width: 56.25rem) {
  .third_section .article {
    grid-template-columns: repeat(2, 1fr);
    transform: translate(0, 0);
  }
}
@media (max-width: 37.5rem) {
  .third_section .article {
    grid-template-columns: 1fr;
    transform: translate(0, 0);
  }
}
.third_section .article__card {
  background: white;
  border-radius: 5px;
}
.third_section .article__card__img {
  aspect-ratio: 1.2/1;
}
.third_section .article__card__img img {
  width: 100%;
  height: 100%;
  border-radius: 5px 5px 0 0;
}
.third_section .article__card__contents {
  padding: 30px 25px;
}
.third_section .article__card__contents small {
  font-size: 0.625rem;
  color: #9698a6;
  font-family: "Public Sans", sans-serif;
  font-weight: 300;
}
.third_section .article__card__contents h4 {
  color: #2d314d;
  font-family: "Public Sans", sans-serif;
  line-height: 1.1;
  font-weight: 400;
  font-size: 1rem;
  margin-top: 14px;
  margin-bottom: 14px;
  cursor: pointer;
  transition: color 300ms ease;
}
.third_section .article__card__contents h4:hover {
  color: #209740;
}
.third_section .article__card__contents p {
  color: #9698a6;
  font-family: "Public Sans", sans-serif;
  font-size: 0.75rem;
  font-weight: 300;
}

footer {
  background-color: #2d314d;
  padding: 50px 0;
}

.footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 56.25rem) {
  .footer {
    grid-template-columns: 1fr;
    transform: translate(0, 0);
  }
}
.footer__left {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 56.25rem) {
  .footer__left {
    place-items: center;
    grid-template-columns: 1fr;
    transform: translate(0, 0);
  }
}
.footer__left__socials {
  display: grid;
  gap: 50px;
}
@media (max-width: 56.25rem) {
  .footer__left__socials {
    place-items: center;
    transform: translate(0, 0);
  }
}
.footer__left__socials__svg {
  display: flex;
  gap: 13px;
}
@media (max-width: 56.25rem) {
  .footer__left__socials__svg {
    justify-content: center;
    align-items: center;
    gap: 25px;
    transform: translate(0, 0);
  }
}
.footer__left__socials__svg a svg {
  fill: #fff;
  transition: fill 300ms ease;
}
.footer__left__socials__svg a svg:hover {
  fill: #1c8237;
}
.footer__left__links {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 56.25rem) {
  .footer__left__links {
    text-align: center;
    place-items: center;
    grid-template-columns: 1fr;
    transform: translate(0, 0);
    gap: 20px;
    margin-top: 30px;
    margin-bottom: 30px;
  }
}
.footer__left__links .link {
  display: flex;
  flex-direction: column;
  gap: 20px;
  color: #f3f4f6;
  font-family: "Public Sans", sans-serif;
  font-size: 0.8125rem;
}
.footer__left__links .link a {
  transition: color 300ms ease;
}
.footer__left__links .link a:hover {
  color: #25ad49;
}
.footer__right {
  display: grid;
  place-items: end;
  gap: 26px;
}
@media (max-width: 56.25rem) {
  .footer__right {
    place-items: center;
    transform: translate(0, 0);
  }
}
.footer__right p {
  color: #9698a6;
  font-family: "Public Sans", sans-serif;
  font-size: 0.75rem;
  font-weight: 300;
}